import React from 'react';
import { Button } from '../../../AppStyle';
import { ConfirmWrapper } from './ConfirmStyleComponent';

const Confirm = ({ selectedCategory, handleNotDelCategory, delCategory, lang }) => (
    
    <ConfirmWrapper>
        <p>
            {!lang ? ` ,     "${selectedCategory}"?` : `Are you sure you want to delete the category "${selectedCategory}"?`}
        </p>
        <Button onClick={handleNotDelCategory}>{!lang ? '' : 'Cancel'}</Button>
        <Button onClick={delCategory}></Button><br/><br/>
    </ConfirmWrapper>
);

export default Confirm;